.km-side-nav {
  margin-left: 10px;
}
.km-side-nav .side-nav {
  border-top: 1px solid #BBB;
  padding: 8px 0;
  font-weight: bolder;
  font-size: 12px;
}
.km-side-nav .side-nav:last-child {
  border-bottom: 1px solid #BBB;
}
.km-side-nav .side-nav.expanded {
  font-size: 15px;
  padding-top: 0;
}
.km-side-nav .side-nav.expanded > .title {
  background-color: #EEE;
  border: 1px solid #D9D9D9;
  padding: 6px;
}
.km-side-nav .side-nav > .side-nav-intermediate {
  display: none;
  border-top: 1px solid #BBB;
  padding: 8px 0;
  font-weight: bolder;
  font-size: 12px;
}
.km-side-nav .side-nav > .side-nav-intermediate:last-child {
  border-bottom: 0;
}
.km-side-nav .side-nav > .side-nav-intermediate:first-child {
  border-top: 0;
}
.km-side-nav .side-nav > .side-nav-intermediate.expanded {
  font-size: 12px;
  margin-left: 15px;
}
.km-side-nav .side-nav > .side-nav-intermediate.expanded > .title {
  background-color: #EEE;
  border: 1px solid #D9D9D9;
  padding: 4px;
}
.km-side-nav .side-nav > .side-nav-intermediate > .title {
  padding-left: 20px;
  font-weight: bold;
}
.km-side-nav .side-nav > .side-nav-intermediate > .links {
  padding-left: 20px;
  font-weight: normal;
}
.km-side-nav .side-nav > .side-nav-intermediate > .links > .link.selected {
  margin-bottom: 10px;
  text-decoration: none;
}
.km-side-nav .title {
  cursor: pointer;
  display: block;
}
.km-side-nav .links {
  display: none;
  padding-left: 15px;
  font-weight: normal;
  font-size: 12px;
}
.km-side-nav .links.active {
  display: block;
}
.km-side-nav .link {
  cursor: pointer;
  display: block;
  padding: 5px;
  margin-top: 5px;
}
.km-side-nav .link:hover {
  color: #E57171;
  font-weight: bolder;
  text-decoration: none;
}
.km-side-nav .link.selected {
  border: 1px solid #D8D6D6;
  background-color: #F8F7F7;
  color: #E57171;
  font-weight: bolder;
}
